import { generateFormdata, generateWatchers } from '../utils'

/**
 * @typedef {object} Link
 * @property {string} pagePath - 
 * @property {string} iconPath - 
 * @property {string} selectedIconPath - 
 * @property {string} text - 
 */

/**  */
export function getProps() {
  return {
    /**
     * 
     * @type {Link[]}
     */
    links: {
      type: Array,
      default: () => [],
    },
    /**  */
    textColor: {
      type: String,
      default: '#333333',
    },
    /**  */
    selectedTextColor: {
      type: String,
      default: '#333333',
    },
    /**  */
    backgroundColor: {
      type: String,
      default: '#ffffff',
    },
  }
}

/**  */
export function getFormdata() {
  const formdata = generateFormdata(getProps())
  return formdata
}

/**  */
export function getWatchers() {
  const watchers = generateWatchers(getProps())
  return watchers
}

/**  */
export function getMeta() {
  return {
    name: 'tabbar',
    label: '',
    cate: '',
    // count: 0,
    limit: 1,
    data: getFormdata(),
  }
}

/**  */
export function getRules() {
  return {
    links: {
      type: 'array',
      required: true,
      min: 2,
      message: '',
      defaultField: {
        type: 'object',
        fields: {
          iconPath: {type: 'string', required: true, message: ''},
          selectedIconPath: {type: 'string', required: true, message: ''},
          text: {type: 'string', required: true, message: ''},
        }
      },
      trigger: ['change','blur'],
    }
  }
}

